.air-quality-wrapper {
	position: relative;
	border: 2px solid black;
	height: 320px;
	width: 300px;
	border-radius: 20px;
	backdrop-filter: blur(20px);
	background: #fff4;
}

.air-quality-label {
	position: absolute;
	margin: 0;
	height: 2rem;
	width: 90%;
	top: 10px;
	left: 5%;
	font-size: 1.6rem;
}

.air-quality-meter {
	position: absolute;
	width: 240px;
	top: calc(3rem + 6px);
	left: 30px;
}

.air-quality-needle {
	position: absolute;
	width: 38px;
	left: calc(50% - 20px);
	top: calc(3rem + 25px);
}

.air-quality-number {
	position: absolute;
	border: 5px solid #444;
	border-radius: 7.4rem;
	margin: 0;
	top: calc(3rem + 64px);
	left: calc(50% - 3.7rem - 7px);
	height: 7.4rem;
	width: 7.4rem;
	line-height: 7.6rem;
}

.air-quality-description {
	position: absolute;
	top: 234px;
	left: 5%;
	height: 1.5rem;
	width: 90%;
	font-size: 1.5rem;
}
